import Vue from 'vue';
import i18n from '../../../../i18n';
export default Vue.extend({
    i18n: i18n('mobile/views/pages/user/home.notes.vue'),
    props: ['user'],
    data() {
        return {
            fetching: true,
            notes: []
        };
    },
    mounted() {
        this.$root.api('users/notes', {
            userId: this.user.id,
        }).then(notes => {
            this.notes = notes;
            this.fetching = false;
        });
    }
});
